<template>
  <div class="views" id="views">
    <div id="container" class="container"></div>
  </div>
</template>

<script>
import { App } from '@/assets/App/App'
import { GUI } from 'three/examples/jsm/libs/dat.gui.module'

const app = new App()
export default {
  data () {
    return {
      params: {
        methods: {
          animation: true, // 循环调用渲染
          renderer: true, // 应用渲染器
          light: true, // 在场景中加入全景光
          controls: true, // 使用轨道控制器，鼠标滚轮控制放大缩小，左键拖动绕中心点旋转，右键拖动中心点。
          statement: true // 在控制台打印声明，与本案例无关，可忽略。
        },
        events: {
          windowResize: true // 监听窗口尺寸变化，并改变渲染页面尺寸
        },
        params: {
          container: null, // 渲染结果输出dom页面
          renderer: [
            'WebGLRenderer'
          ],
          controls: [
            'OrbitControls'
          ],
          rendererParams: {
            antialias: true // 抗锯齿，可忽略。
          }
        }
      },
      orbitParams: {
        enableDamping: true, // 是否使用阻尼（可以理解为是否使用惯性）
        dampingFactor: 0.25, // 阻尼系数（鼠标拖拽旋转灵敏度）
        enableZoom: true, // 是否可以缩放
        autoRotate: true, // 是否自动旋转
        autoRotateSpeed: 1, // 自动旋转速度
        minDistance: 1, // 相机距离中心点的最近距离
        maxDistance: 2000, // 相机距离中心点的最远距离
        minPolarAngle: 0, // 相机可仰视角度，单位PI
        maxPolarAngle: 1, // 相机可俯视角度，单位PI
        enablePan: true // 是否可以右键拖拽
      }
    }
  },
  mounted () {
    this.params.params.container = document.getElementById('container') // 获取渲染结果输出dom页面
    /**
     * 初始化场景
     */
    app.init(this.params, (scene, camera, renderer) => {
      camera.position.set(1000, 100, 1000) // 修改相机位置
    })
    /**
     * 导入模型
     */
    app.addModel({
      url: 'static/model/fbx/cqcet/15/实训楼.FBX', // 模型地址
      type: 'fbx' // 模型格式
    }, () => { // 加载完成回调，可忽略。
      console.log('模型加载完成')
    })
    /**
     * 使用gui调试轨道控制器
     */
    var gui = new GUI({ width: 300 })
    this.gui = gui
    gui.add(this.orbitParams, 'enableDamping').onChange(function (value) {
      app.orbitControls.enableDamping = value
    })
    gui.add(this.orbitParams, 'dampingFactor', 0.05, 1).onChange(function (value) {
      app.orbitControls.dampingFactor = Number(value)
    })
    gui.add(this.orbitParams, 'enableZoom').onChange(function (value) {
      app.orbitControls.enableZoom = value
    })
    gui.add(this.orbitParams, 'autoRotate').onChange(function (value) {
      app.orbitControls.autoRotate = value
    })
    gui.add(this.orbitParams, 'autoRotateSpeed', 1, 100).onChange(function (value) {
      app.orbitControls.autoRotateSpeed = Number(value)
    })
    gui.add(this.orbitParams, 'minDistance', 1, 100).onChange(function (value) {
      app.orbitControls.minDistance = Number(value)
    })
    gui.add(this.orbitParams, 'maxDistance', 100, 10000).onChange(function (value) {
      app.orbitControls.maxDistance = Number(value)
    })
    gui.add(this.orbitParams, 'minPolarAngle', 0.0, 1.0).onChange(function (value) {
      app.orbitControls.minPolarAngle = Number(value) * Math.PI
    })
    gui.add(this.orbitParams, 'maxPolarAngle', 0.0, 1.0).onChange(function (value) {
      app.orbitControls.maxPolarAngle = Number(value) * Math.PI
    })
    gui.add(this.orbitParams, 'enablePan').onChange(function (value) {
      app.orbitControls.enablePan = value
    })
  },
  beforeDestroy () {
    this.gui.destroy()
  }
}
</script>

<style scoped>
.views {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
}
</style>
